import React, { useEffect, useState, useContext } from "react";
import { Row, Col } from "react-bootstrap";
import "./Home.css";
import { Link } from "react-router-dom";
import Spline from "@splinetool/react-spline";
import MyLoader from "../../Common/MyLoader/MyLoader";
import AutoNavigateContext from '../../Common/Contexts/Contexts';

const Home = () => {
  const { toggleAutoNavigate } = useContext(AutoNavigateContext);

  const [isLoading, setIsLoading] = useState(true);
  const jobs = [
    "FullStack Web Developer",
    "Amazon VA",
    "Google Ads Manager",
    "Data Entry",
    "Store Manager",
    "GoHighLevel",
    "Ninja Automations",
  ];
  const [role, setRole] = useState("");
  useEffect(() => {
    let index = 0;
    const intervalId = setInterval(() => {
      setRole(jobs[index]);
      // eslint-disable-next-line react-hooks/exhaustive-deps
      index = (index + 1) % jobs.length;
    }, 2000);

    return () => clearInterval(intervalId); // Cleanup interval on component unmount
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <Row className="row-content">
        <Col xs={12} md={6} className="d-flex flex-column align-items-center justify-content-center">
        <div className="spline">
        {isLoading && <MyLoader />}
          <Spline
            scene="https://prod.spline.design/7hQVzQV-tjbJ4NMR/scene.splinecode"
            onLoad={() => setIsLoading(false)}
          />
        </div>
        </Col>
        <Col xs={12} md={6} className="d-flex flex-column align-items-center justify-content-center">
          <h2>Hi! I am</h2>
          <h1 className="name">Anthony</h1>
          <h3 className="role">{role}</h3>
        </Col>
        <Col style={{maxHeight: "20vh"}} className="d-flex justify-content-around align-items-center flex-wrap">
        <Link onClick={toggleAutoNavigate} className="btn btn-primary" >Advertisement</Link>
        <Link className="btn btn-primary" to={"/about"}>Work Experience</Link>
        <Link className="btn btn-primary" to={"/credits"}>Next Page</Link>
        </Col>
      </Row>
    </>
  );
};

export default Home;
